* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

nav {
  height: 70px;
  display: flex;
  padding: 20px 36px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

nav .Airbnb-logo {
  max-width: 100px;
}

.photoGrid {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 25px;
}

.photoGrid img {
  height: 600px;
  width: 1150px;
}

.experiences {
  padding: 10px 50px;
  width: 700px;
}

.experiences h2 {
  font-size: 48px;
  font-weight: 600;
}

.experiences h4 {
  font-size: 30px;
  font-weight: 200;
}

.gray {
  color: #918E9B;
}

section {
  padding: 50px;
}

.card-list {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  gap: 20px;
}

card {
  position: relative;
  width: 175px;
  font-size: 12px;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
}


.photo {
  width: 100%;
  border-radius: 9px;
  margin-bottom: 9px;
}

card button {
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}

.card-title {
  overflow: hidden;
  text-overflow: ellipsis;
}

.card-stats {
  display: flex;
  align-items: center;
}

.card-star {
  height: 14px;
}

.card-price {
  margin-top: auto;
}